import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Form, Upload, Collapse, Table, Button } from 'antd';
import actions from '../../redux/claim/actions';
import UploadImg from './UploadButtonBill.png';
import UploadStyleWrapper from './upload.style';
import moment from 'moment';
import FormBillingInfoDetailsSub from './billingInfoDetailsSub';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import SuperFetch from '../../helpers/superFetch';
import FileDownloader from '../../helpers/fileDownloader';
const TitleSvgComponent = props => (
  <svg {...props} width={236} height={33}>
    <text
      data-name="Visit_1(12 Jan 2020):"
      transform="translate(0 27)"
      fill="#f17d27"
      fontSize={25}
      fontFamily="SegoeUI-Bold, Segoe UI"
      fontWeight={700}
    >
      <tspan x={0} y={0}>
        Visit {props.name}
      </tspan>
      <tspan y={0} fontFamily="SegoeUI, Segoe UI" fontWeight={400}>
        {props.date ? props.date : null}
      </tspan>
    </text>
  </svg>
);

class BillingInfoForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      BillingInfoFiles: this.props.visit.BillingInfoFiles,
    };
    this.getAlert = this.getAlert.bind(this);
    this.clickChild = {};
  }

  componentDidMount() {
    this.props.setClick(this.getAlert);
  }

  getAlert() {
    return new Promise((resolve, reject) => {
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          var visit = this.props.visit;
          visit.BillingInfoFiles = this.state.BillingInfoFiles;

          const tasks = Object.values(this.clickChild).map(fn => {
            return fn();
          });
          Promise.all(tasks)
            .then(billingInfos => {
              var merged = [].concat.apply([], billingInfos);

              visit.BillingInfos = merged;
              resolve(visit);
            })
            .catch(err => console.log(err));
        } else {
          reject({ visit, err: 'not completed' });
        }
      });
    });
  }

  render() {
    const { visit } = this.props;
    const rowBlockstyling = {
      marginBottom: '5%',
    };
    const customPanelStyle = {
      background: '#0F305E',
      paddingBottom: 24,
      border: 0,
      overflow: 'hidden',
    };
    const columns = [
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        render: (text, record) => {
          console.log(record);
          return (
            <Button
              type="link"
              onClick={e => {
                FileDownloader(record.url, record.name);
              }}
            >
              {text}
            </Button>
          );
        },
      },
      {
        title: 'Action',
        key: 'action',
        render: (text, file) => (
          <div className="isoInvoiceBtnView">
            <Button
              icon="download"
              // icon="delete"
              onClick={() => {
                console.log(file);
                FileDownloader(file.url, file.name);
              }}
            ></Button>
            <Button
              icon="delete"
              // icon="delete"
              onClick={() => {
                SuperFetch.delete('claim/billingInfoFiles', { id: file.id })
                  .then(response => {
                    var newBillingInfoFiles = this.state.BillingInfoFiles;
                    var index = newBillingInfoFiles.findIndex(
                      item => item.id === response.data.id
                    );
                    //https://www.codementor.io/lautiamkok/javascript-tips-searching-the-index-of-an-element-and-delete-or-replace-it-cfpy6uasy
                    newBillingInfoFiles.splice(index, 1);

                    this.setState({ BillingInfoFiles: newBillingInfoFiles });
                  })
                  .catch(e => console.log(e));
              }}
            ></Button>
          </div>
        ),
      },
    ];
    return (
      <Form style={rowBlockstyling}>
        <Row
          type="flex"
          justify="space-between"
          name="currency"
          style={rowBlockstyling}
        >
          <Col>
            <TitleSvgComponent
              date={
                visit.dateOfAdmissionVisit == null
                  ? null
                  : moment(visit.dateOfAdmissionVisit).format('DD MMM YYYY')
              }
            />
          </Col>
        </Row>
        <Row name="data">
          <Collapse bordered={false}>
            {this.props.BenefitsLabel.map(category => (
              <Collapse.Panel
                showArrow={false}
                header={
                  <Row>
                    <Col span={12}>
                      <span style={{ fontWeight: 'bold', color: '#0F305E' }}>
                        {category.name}
                      </span>
                    </Col>
                    <Col span={6}>{this.props.visit.billingCurrency}</Col>
                    <Col span={6}>USD</Col>
                  </Row>
                }
                key={category.id}
                style={customPanelStyle}
              >
                <Row>
                  <FormBillingInfoDetailsSub
                    BenefitSubCategories={category.BenefitSubCategories}
                    visitId={visit.id}
                    categoryId={category.id}
                    currency={this.props.visit.billingCurrency}
                    currencyRate={this.props.visit.billingRate}
                    //parentCallback = {this.callbackFunction}
                    billingInfosSub={visit.BillingInfos.filter(
                      item => item.billingCat === category.id
                    )}
                    updateTotal={this.props.updateTotal}
                    setClick={click => {
                      this.clickChild[category.id] = click;
                    }}
                    otherName={
                      visit.BillingInfoOtherNames[0]
                        ? visit.BillingInfoOtherNames[0].value
                        : null
                    }
                  />
                </Row>
              </Collapse.Panel>
            ))}
          </Collapse>
        </Row>
        <Row name="tabledata" style={rowBlockstyling}>
          {this.state.BillingInfoFiles.length > 0 && (
            <Table
              bordered={false}
              rowKey="id"
              dataSource={this.state.BillingInfoFiles}
              columns={columns}
              pagination={false}
            />
          )}
        </Row>
        <UploadStyleWrapper name="uploadForm" style={{ width: 300 }}>
          <Upload
            action="/api/claim/upload"
            headers={{
              Authorization: localStorage.getItem('id_token') || undefined,
            }}
            onChange={info => {
              switch (info.file.status) {
                case 'done':
                  const newData = {
                    visitId: visit.id,
                    url: info.file.response.path,
                    name: info.file.name,
                  };
                  //notification('success', "File uploaded" );
                  SuperFetch.post('claim/insertBillingInfoFiles', newData)
                    .then(response => {
                      if (response.status === 200) {
                        this.setState({
                          BillingInfoFiles: [
                            ...this.state.BillingInfoFiles,
                            newData,
                          ],
                        });
                      } else {
                        notification(
                          'error',
                          'Something wrong in retrieving data'
                        );
                      }
                    })
                    .catch(e => console.log(e));

                  break;
                default:
                  break;
              }
            }}
          >
            <img src={UploadImg} alt="UploadImg" />
          </Upload>
        </UploadStyleWrapper>
      </Form>
    );
  }
}

const WrappedBillingInfoForm = Form.create({ name: 'BillingInfoForm' })(
  BillingInfoForm
);

export default connect(state => {
  return {};
}, actions)(WrappedBillingInfoForm);
