import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Form, Upload, Table, Button } from 'antd';
import UploadImg from './UploadButton.png';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import actions from '../../redux/claim/actions';
import moment from 'moment';
//import SuperFetch from '../../helpers/superFetch';
import {
  EditableFormRow,
  EditableCell,
} from '@isomorphic/shared/isomorphic/components/Tables/CustomizedCells';
import FileDownloader from '../../helpers/fileDownloader';
const TitleSvgComponent = props => (
  <svg {...props} width={236} height={33}>
    <text
      data-name="Visit_1(12 Jan 2020):"
      transform="translate(0 27)"
      fill="#f17d27"
      fontSize={25}
      fontFamily="SegoeUI-Bold, Segoe UI"
      fontWeight={700}
    >
      <tspan x={0} y={0}>
        Visit {props.name}
      </tspan>
      <tspan y={0} fontFamily="SegoeUI, Segoe UI" fontWeight={400}>
        ({props.date})
      </tspan>
    </text>
  </svg>
);

class DocumentDetailsForm extends Component {
  constructor(props) {
    super(props);
    this.columns = [
      {
        title: 'Documents',
        dataIndex: 'name',
        width: '30%',
        render: (text, record) => {
          return (
            <Button
              type="link"
              onClick={e => {
                FileDownloader(record.url, record.name);
              }}
            >
              <span style={{ color: '#1890ff' }}>{text}</span>
            </Button>
          );
        },
      },
      {
        title: 'Notes',
        dataIndex: 'notes',
        editable: true,
      },
      // {
      //   title: 'operation',
      //   dataIndex: 'operation',
      //   render: (text, file) => (
      //     <div className="isoInvoiceBtnView">
      //       <Button
      //         icon="download"
      //         // icon="delete"
      //         onClick={() => {
      //           console.log(file)
      //           FileDownloader(file.url,file.name)
      //         }}
      //       >
      //       </Button>
      //       <Button
      //         icon="delete"
      //         // icon="delete"
      //         onClick={() => {
      //
      //           SuperFetch.delete('claim/documentsFiles',{id:file.id})
      //           .then(response => {
      //             var newDocumentsFiles = this.state.dataSource
      //             var index = newDocumentsFiles.findIndex(item => item.id === response.data.id)
      //             //https://www.codementor.io/lautiamkok/javascript-tips-searching-the-index-of-an-element-and-delete-or-replace-it-cfpy6uasy
      //             newDocumentsFiles.splice(index,1)
      //
      //             this.setState({ dataSource: newDocumentsFiles })
      //           })
      //           .catch(e => console.log(e))
      //         }}
      //       >
      //
      //       </Button>
      //     </div>
      //   )
      // },
    ];

    this.state = {
      dataSource: this.props.visit.DocumentsFiles,
    };
  }
  handleDelete = key => {
    const dataSource = [...this.state.dataSource];
    this.setState({ dataSource: dataSource.filter(item => item.key !== key) });
  };

  handleSave = row => {
    const newData = this.state.dataSource;
    const index = newData.findIndex(item => row.id === item.id);
    newData[index].notes = row.notes;
    this.setState({ dataSource: newData });

    this.props.insertDocuments({ id: row.id, url: row.url, notes: row.notes });
  };

  render() {
    const { visit } = this.props;

    const { dataSource } = this.state;

    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell,
      },
    };
    const columns = this.columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          handleSave: this.handleSave,
        }),
      };
    });

    const rowBlockstyling = {
      marginBottom: '5%',
    };

    return (
      <Form style={rowBlockstyling} onSubmit={this.handleSubmit}>
        <Row
          type="flex"
          justify="space-between"
          name="currency"
          style={rowBlockstyling}
        >
          <TitleSvgComponent
            date={
              visit.dateOfAdmissionVisit == null
                ? null
                : moment(visit.dateOfAdmissionVisit).format('DD MMM YYYY')
            }
          />
        </Row>
        <Row name="tabledata" style={rowBlockstyling}>
          {dataSource.length > 0 && (
            <Table
              bordered={false}
              components={components}
              rowClassName={() => 'editable-row'}
              dataSource={dataSource}
              columns={columns}
              pagination={false}
              className={'someruame'}
              rowKey="id"
            />
          )}
        </Row>
        <Row name="upload" style={{ width: 300 }}>
          <Upload
            action="/api/claim/uploaddocument"
            headers={{
              Authorization: localStorage.getItem('id_token') || undefined,
            }}
            data={{ visitId: visit.id }}
            showUploadList={false}
            onChange={info => {
              switch (info.file.status) {
                case 'done':
                  const newData = {
                    id: info.file.response.id,
                    visitId: visit.id,
                    url: info.file.response.url,
                    name: info.file.name,
                  };
                  notification('success', 'File uploaded');

                  this.setState({
                    dataSource: [...dataSource, newData],
                  });
                  break;
                case 0:
                  break;
                default:
                  break;
              }
            }}
          >
            <img src={UploadImg} alt="UploadImg" />
          </Upload>
          ,
        </Row>
      </Form>
    );
  }
}

const WrappedDocumentDetailsForm = Form.create({ name: 'BillingInfoForm' })(
  DocumentDetailsForm
);

export default connect(state => {
  return {};
}, actions)(WrappedDocumentDetailsForm);
