import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Comment, Avatar, Form, Button, List, Input } from 'antd';
import moment from 'moment';
import jwtDecode from 'jwt-decode';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import SuperFetch from '../../helpers/superFetch';
import CommentsStyleWrapper from './comments.style';
const { TextArea } = Input;

const CommentList = ({ comments }) => (
  <List
    dataSource={comments}
    header={`${comments.length} ${comments.length > 1 ? 'replies' : 'reply'}`}
    itemLayout="horizontal"
    renderItem={props => <Comment {...props} />}
  />
);

const Editor = ({ onChange, onSubmit, submitting, value }) => (
  <div>
    <Form.Item>
      <TextArea rows={4} onChange={onChange} value={value} />
    </Form.Item>
    <Form.Item>
      <Button htmlType="submit" loading={submitting} onClick={onSubmit} type="primary">
        Add Comment
      </Button>
    </Form.Item>
  </div>
);

class Comments extends Component {
  state = {
    comments: [],
    submitting: false,
    value: '',
  };

  componentDidMount() {

    if (this.props.claim.claimID){
      //var claimID = base64url(this.props.claim.claimID)
      SuperFetch.get('comments/comments',{id:this.props.claim.claimID})
      .then(response => {

        if(response.status === 200) {
          var newComments = response.data.map(comment => {
            return {author: comment.createdBy,
            avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
            content: comment.message,
            datetime: moment(comment.createdAt).fromNow()}
          })
          this.setState({
            submitting: false,
            value: '',
            comments: newComments,
          });
          //this.setState({claim:response.data});
          //notification('succuess', "done" );
        } else {
          notification('error', "Something wrong in retrieving comments data" );
        }
      })
      .catch(e => console.log(e))

    } else {
      notification('error', "No soch claim" );
    }

  }
  handleSubmit = () => {
    if (!this.state.value) {
      notification('error', "Please enter your message" );
      return;
    }
    if (!this.props.claim.claimID) {
      notification('error', "Please generate Claim ID" );
      return;
    }

    this.setState({
      submitting: true,
    });

    const profile = jwtDecode(this.props.idToken);

    var comment = {
      claimInfoId:this.props.claim.claimID,
      group:'regular',
      message:this.state.value,
      createdBy:profile.id
    }

    SuperFetch.post('comments/comments',comment)
    .then(response => {

      if(response.status === 200) {

        this.setState({
          submitting: false,
          value: '',
          comments: [
            {
              author: profile.username,
              avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
              content: <p>{this.state.value}</p>,
              datetime: moment(response.data.createdAt).fromNow(),
            },
            ...this.state.comments,
          ],
        });
        //notification('succuess', "done" );
      } else {
        notification('error', "Something wrong in postsing comments" );
      }
    })
    .catch(e => console.log(e))

  };

  handleChange = e => {
    this.setState({
      value: e.target.value,
    });
  };

  render() {
    const { comments, submitting, value } = this.state;

    return (
      <CommentsStyleWrapper>
        {comments.length > 0 && <CommentList comments={comments} />}
        <Comment
          avatar={
            <Avatar
              src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png"
              alt="Han Solo"
            />
          }
          content={
            <Editor
              onChange={this.handleChange}
              onSubmit={this.handleSubmit}
              submitting={submitting}
              value={value}
            />
          }
        />
      </CommentsStyleWrapper>
    );
  }
}


export default connect(
  state => {

    return {idToken: state.Auth.idToken, claim:state.Claim}
  },
  {}
)(Comments);
