import axios from 'axios';
import FileSaver from 'file-saver';
import notification from '@isomorphic/shared/isomorphic/components/Notification';

// localStorage.js
const FileDownloader = (url, name) => {

    axios.get(`/api/files?url=`+url, {responseType: 'blob',timeout: 30000}
    )
   .then((response) => {
     FileSaver.saveAs(response.data, name)
   })
   .catch(e => {
     console.log(e)
     notification('error', "Something wrong in downloading data" )
   });

};
export default FileDownloader;
