import React from 'react';
import { connect } from 'react-redux';
import {
  Modal,
  Form,
  Input,
  DatePicker,
  Select,
  Upload,
  Button,
  Table,
  Col,
  Row,
} from 'antd';
import UploadImg from './UploadButton.png';
import * as countries from 'i18n-iso-countries';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import moment from 'moment';
import FileDownloader from '../../helpers/fileDownloader';
const { Option } = Select;

const CollectionCreateForm = Form.create({ name: 'form_in_modal' })(
  // eslint-disable-next-line
  class extends React.Component {
    constructor(props) {
      super(props);
      countries.registerLocale(require('i18n-iso-countries/langs/en.json'));
      this.state = {
        countries: countries.getNames('en'),
        exchangeRate: [],
        claimInfoVisitsFiles: [],
      };
    }

    componentDidMount() {
      SuperFetch.get('constant/getCurrencyRate')
        .then(response => {
          if (response.status === 200) {
            this.setState({ exchangeRate: response.data });
          } else {
            notification('error', "Something wrong in retrieving Currency'");
          }
        })
        .catch(e => console.log(e));

      this.props.onRef(this);
    }

    setVisit() {
      if (this.props.visit) {
        var data = {};
        data.hospitalOrClinicName = this.props.visit.hospitalOrClinicName;
        data.hospitalOrClinicCountryrl = this.props.visit.hospitalOrClinicCountryrl;
        data.hospitalOrclinicEmail = this.props.visit.hospitalOrclinicEmail;
        data.MedicalDiagnosis = this.props.visit.MedicalDiagnosis;
        data.dateOfAdmissionVisit = this.props.visit.dateOfAdmissionVisit
          ? moment(this.props.visit.dateOfAdmissionVisit)
          : null;
        data.hospitalOrClinicCountry = this.props.visit.hospitalOrClinicCountry;
        data.doctorName = this.props.visit.doctorName;
        data.billingCurrency = this.props.visit.billingCurrency;

        this.props.form.setFieldsValue(data);
        this.setState({
          claimInfoVisitsFiles: this.props.visit.ClaimInfoVisitsFiles,
        });
      }
    }

    normFile = e => {
      if (Array.isArray(e)) {
        return e;
      }
      return e && e.fileList;
    };
    render() {
      const { visible, onCancel, onCreate, form, visit } = this.props;
      const { getFieldDecorator } = form;

      const columns = [
        {
          title: 'Name',
          dataIndex: 'name',
          key: 'name',
        },
        {
          title: 'Action',
          key: 'action',
          render: (text, file) => {
            return (
              <div className="isoInvoiceBtnView">
                <Button
                  icon="download"
                  // icon="delete"
                  onClick={() => {
                    console.log(file);
                    FileDownloader(file.url, file.name);
                    // SuperFetch.get('files/file',{url:file.url})
                    // .then(response => {
                    //   fileDownload(response.data, file.name);
                    //
                    // })
                    // .catch(e => console.log(e))
                  }}
                ></Button>
                <Button
                  icon="delete"
                  // icon="delete"
                  onClick={() => {
                    SuperFetch.delete('claim/claimInfoVisitsFile', {
                      id: file.id,
                    })
                      .then(response => {
                        var newClaimInfoVisitsFiles = this.state
                          .claimInfoVisitsFiles;
                        var index = newClaimInfoVisitsFiles.findIndex(
                          item => item.id === response.data.id
                        );
                        //https://www.codementor.io/lautiamkok/javascript-tips-searching-the-index-of-an-element-and-delete-or-replace-it-cfpy6uasy
                        newClaimInfoVisitsFiles.splice(index, 1);

                        this.setState({
                          claimInfoVisitsFiles: newClaimInfoVisitsFiles,
                        });
                      })
                      .catch(e => console.log(e));
                  }}
                ></Button>
              </div>
            );
          },
        },
      ];

      return (
        <Modal
          visible={visible}
          title="Create a new visit"
          okText="Create"
          onCancel={onCancel}
          onOk={onCreate}
          footer={[
            <Button key="cancel" type="primary" onClick={onCancel}>
              Cancel
            </Button>,
            <Button key="submit" type="primary" onClick={onCreate}>
              Submit
            </Button>,
          ]}
        >
          <Form>
            <Row>
              <Col span={10}>
                <Form.Item
                  colon={false}
                  label={<span>Hospital or Clinic Name:</span>}
                >
                  {getFieldDecorator('hospitalOrClinicName', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input Hospital or Clinic Name',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
                <Form.Item
                  colon={false}
                  label={<span>Hospital or Clinic url:</span>}
                >
                  {getFieldDecorator('hospitalOrClinicCountryrl', {
                    rules: [],
                  })(<Input />)}
                </Form.Item>
                <Form.Item
                  colon={false}
                  label={<span>Hospital or Clinic Email:</span>}
                >
                  {getFieldDecorator('hospitalOrclinicEmail', {
                    rules: [],
                  })(<Input />)}
                </Form.Item>
                <Form.Item
                  colon={false}
                  label={<span>Medical Diagnosis:</span>}
                >
                  {getFieldDecorator('MedicalDiagnosis', {
                    rules: [],
                  })(<Input />)}
                </Form.Item>
              </Col>
              <Col span={10} offset={2}>
                <Form.Item
                  colon={false}
                  label={<span>Date of Admission/Visit:</span>}
                >
                  {getFieldDecorator('dateOfAdmissionVisit', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input Date of Admission/Visit',
                      },
                    ],
                  })(
                    <DatePicker
                      style={{ width: '100%' }}
                      format={'DD MMM YYYY'}
                    />
                  )}
                </Form.Item>
                <Form.Item
                  colon={false}
                  label={<span>Hospital or Clinic Country:</span>}
                >
                  {getFieldDecorator('hospitalOrClinicCountry', {
                    rules: [],
                  })(
                    <Select
                      style={{ width: '100%' }}
                      placeholder="Select country..."
                      showSearch
                      filterOption={(input, option) => {
                        return (
                          option.props.children
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0 ||
                          option.props.value
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0
                        );
                      }}
                    >
                      {Object.entries(this.state.countries).map(
                        ([code, name]) => (
                          <Option key={code} value={code}>
                            {name}
                          </Option>
                        )
                      )}
                    </Select>
                  )}
                </Form.Item>

                <Form.Item colon={false} label={<span>Doctor Name:</span>}>
                  {getFieldDecorator('doctorName', {
                    rules: [],
                  })(<Input />)}
                </Form.Item>

                <Form.Item colon={false} label={<span>Billing Currency:</span>}>
                  {getFieldDecorator('billingCurrency', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input Billing Currency',
                      },
                    ],
                  })(
                    <Select
                      placeholder="Select or enter..."
                      showSearch
                      filterOption={(input, option) => {
                        return (
                          option.props.children[0]
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0 ||
                          option.props.children[2]
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0
                        );
                      }}
                    >
                      {this.state.exchangeRate
                        ? this.state.exchangeRate.map(currency => (
                            <Select.Option
                              value={currency.code}
                              key={currency.code}
                              date={currency.date}
                              rate={currency.rate}
                            >
                              {currency.name} - {currency.code}
                            </Select.Option>
                          ))
                        : null}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </Row>
            {visit && visit.ClaimInfoVisitsFiles ? (
              <Table
                columns={columns}
                dataSource={this.state.claimInfoVisitsFiles}
                rowKey="id"
                width="100%"
              />
            ) : null}

            <Form.Item label={<span>Claim Forms:</span>}>
              {getFieldDecorator('ClaimInfoVisitsFiles', {
                valuePropName: 'fileList',
                getValueFromEvent: this.normFile,
              })(
                <Upload
                  action="/api/claim/upload"
                  headers={{
                    Authorization:
                      localStorage.getItem('id_token') || undefined,
                  }}
                  name="files"
                >
                  <img src={UploadImg} alt="UploadImg" />
                </Upload>
              )}
            </Form.Item>
          </Form>
        </Modal>
      );
    }
  }
);

export default connect(state => {
  return {};
}, {})(CollectionCreateForm);
